import React from 'react';

export default ({ title, id, done, handleToggleFoo, handleDeleteFoo,handleChangeTitle }) => <tr>
  <td><input type="checkbox"
    checked={done}
    onChange={e => handleToggleFoo(id, e.target.checked)}
  /></td>
  <td>{id}</td>
  <td><input type="text"
    defaultValue={title}
    onChange={e => handleChangeTitle(id, e.target.value)}
  /></td>
  <td><button className="btn btn-sm btn-primary" type="button" onClick={() => handleDeleteFoo(id)}>Delete</button></td>
</tr>;
