
import db from '../IDB/db.js';
import React, { Component } from 'react';
import AddFoo from './AddFoo';
import FooTable from './FooTable';

export default class Foos extends Component {
    constructor() {
        super();
        this.state = { foos: [] };
        this.handleAddFoo = this.handleAddFoo.bind(this);
        this.handleDeleteFoo = this.handleDeleteFoo.bind(this);
        this.handleToggleFoo = this.handleToggleFoo.bind(this);
        this.handleChangeTitle = this.handleChangeTitle.bind(this);
        
    }

    componentDidMount() {
        db.table('foos')
            .toArray()
            .then(foos => {
                this.setState({ foos });
            });
    }

    handleAddFoo(title) {
        const foo = {
            title,
            done: false,
        };
        db.table('foos')
            .add(foo)
            .then(id => {
                const newList = [...this.state.foos, Object.assign({}, foo, { id })];
                this.setState({
                    foos: newList
                });
            });
    }

    handleToggleFoo(id, done) {
        db.table('foos')
            .update(id, { done })
            .then(() => {
                const fooToUpdate = this.state.foos.find(foo => foo.id === id);
                const newList = [
                    ...this.state.foos.filter(foo => foo.id !== id),
                    Object.assign({}, fooToUpdate, { done })
                ];
                this.setState({
                    foos: newList
                });
            });
    }

    handleChangeTitle(id, title) {
        db.table('foos')
            .update(id, { title })
            .then(() => {
                const fooToUpdate = this.state.foos.find(foo => foo.id === id);
                const newList = [
                    ...this.state.foos.filter(foo => foo.id !== id),
                    Object.assign({}, fooToUpdate, { title })
                ];
                this.setState({
                    foos: newList
                });
            });
    }

    handleDeleteFoo(id) {
        db.table('foos')
            .delete(id)
            .then(() => {
                const newList = this.state.foos.filter(foo => foo.id !== id);
                this.setState({
                    foos: newList
                });
            });
    }

    render() {
        return <>
            <AddFoo handleAddFoo={this.handleAddFoo} />
            <FooTable
                foos={this.state.foos}
                handleChangeTitle={this.handleChangeTitle}
                handleToggleFoo={this.handleToggleFoo}
                handleDeleteFoo={this.handleDeleteFoo}
            />
        </>;
    }
}
