import React from 'react';
import Foo from './Foo';

export default ({ foos, handleToggleFoo, handleDeleteFoo, handleChangeTitle }) => <table className="table">
  <thead>
    <tr>
      <th>id</th>
      <th>Done</th>
      <th>Title</th>
    </tr>
  </thead>
  <tbody>
    {foos.sort((f1,f2)=>f1.id-f2.id).map(foo => <Foo
      key={foo.id}
      {...foo}
      handleChangeTitle={handleChangeTitle}
      handleToggleFoo={handleToggleFoo}
      handleDeleteFoo={handleDeleteFoo}
    />)}
  </tbody>
</table>;