import React, { useState } from 'react'

export default function FileChooseActLabelFuncSate({ onFileUpload, getFileUploadButtonLabel }) {
  const [file, setFile] = useState(null);
  const onDragOver = event => {
    event.stopPropagation();
    event.preventDefault();
    event.dataTransfer.dropEffect = 'copy';
  };

  const onDrop = async event => {
    event.stopPropagation();
    event.preventDefault();
    const file = event.dataTransfer.files[0];
    if (!file)
      throw new Error(`Only files can be dropped here`);
    setFile(file)
  }

  return (
    <>
      <input
        id="files"
        type="file"
        style={{ display: 'none' }}
        accept=".json"
        onChange={e => setFile(e?.target?.files[0])}
      />
      <label className="btn btn-primary"
        onDragOver={onDragOver}
        onDrop={onDrop}
        htmlFor="files">
        {file
          ? <>Выбран файл: {file.name}. Для выбора другого файла</>
          : <>Файл не выбран. Для выбора файла </>}
        нажмите или перетащите сюда файл
      </label>
      <br />
      <button className="btn btn-primary"
        style={file ?? { display: 'none' }}
        onClick={() => onFileUpload?.(file)}>
        {getFileUploadButtonLabel?.(file)}
      </button>
    </>
  )
}